body {
    font-family: Montserrat;

    .btn-orange-sm,
    .btn-white {
        display: inline-flex;
        justify-content: center;
        text-align: center;
        padding: 13px 0;
        min-width: 160px;
        border-radius: 4px;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        border: 2px solid;
    }

    .btn-orange-sm {
        background: $orange;
        color: $white;
        border-color: $orange;
        transition: all 0.4s ease;

        &:hover {
            background-color: #352F2D;
            color: $white;
            border-color: #352F2D;
            transition: all 0.4s ease;
        }
    }

    .btn-white {
        color: black;
        border-radius: 4px;
        border: 2px solid;
        transition: all 0.5s ease 0s;

        &:hover {
            color: $orange;
            border-color: $orange;
            transition: none;
        }
    }

    .btn-orange-bg {
        display: inline-flex;
        justify-content: space-between;
        text-align: center;
        padding: 13px 20px;
        width: 222px;
        border-radius: 4px;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        background: $orange;
        color: $white;
        margin-top: 40px;
        transition: all 0.5s ease 0s;
    }

    .pages {
        background: url(../images/about-us/bg-logo.png) no-repeat 0 169px/100%;

        @include media("<tablet") {
            background: url(../images/about-us/bg-logo.png) no-repeat 0 31px/100%;
            padding: 80px 0 90px 0;
        }
    }

    .main-mob-logo {

        .mob-logo {
            @include media("<tablet") {
                display: none;
            }
        }
    }
}

.container {
    max-width: 1330px;
    margin: 0 auto;
    padding: 0 15px;
}

h1 {

    @include media("<phone") {
        font-size: 24px;
    }
}

h4 {
    margin-bottom: 50px;
}

/* _________________main-section________________ */

.main-section {
    position: relative;
    background-image: url(../images/main-bg.webp);
    background-position: 50% 50%;
    width: 100%;
    height: 900px;
    overflow: hidden;
    background-repeat: no-repeat;
    background-size: cover;

    @include media("<phone") {
        background: url(../images/mob/mob-main-bg.webp);
        background-color: rgba(237, 237, 237, 1);
        background-repeat: no-repeat;
        background-size: 100% auto;
        background-position: 50% 0;
        height: 100%;
    }

    .main-content {
        padding: 267px 0 207px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        @include media("<phone") {
            padding: 280px 0 32px 0;
        }

        h1 {
            line-height: 59px;
            margin: 53px 0 25px 0;

            @include media("<phone") {
                font-size: 26px;
                line-height: 32px;
                margin: 25px 0 28px 0;
            }
        }

        p {
            font-size: 18px;
            line-height: 22px;
            margin: 0 auto;
            margin-bottom: 66px;
            max-width: 470px;

            @include media("<phone") {
                font-size: 18px;
                line-height: 22px;
                margin-bottom: 52px;
            }
        }
    }

    .main-btn-wrap {

        @include media("<phone") {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        .btn-orange-sm {
            margin-right: 22px;

            @include media("<phone") {
                margin: 0 0 17px 0;
            }
        }
    }

    .mob-main-logo {
        display: none;

        @include media("<phone") {
            display: block;
            margin-bottom: 45px;
        }
    }

    .mob-mouse {
        display: none;

        @include media("<phone") {
            display: block;
            margin-top: 27px;
        }
    }
}


/* ________________upgrade-section_____________ */

.upgrade-section {
    padding: 64px 0 70px 0;

    @include media("<phone") {
        padding: 52px 0 92px 0;
    }

    h4 {
        text-align: center;
        line-height: 27px;
        margin-bottom: 20px;

        @include media("<phone") {
            margin-bottom: 22px;
        }
    }

    .total {
        display: flex;
        justify-content: space-around;

        @include media("<desktop") {
            flex-wrap: wrap;
        }

        @include media("<phone") {
            flex-direction: column;
        }

        .item {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 30px;
            padding: 0 15px;

            @include media("<phone") {
                max-width: 310px;
                margin: 0 auto;
                margin-bottom: 62px;

                &:last-child {
                    margin-bottom: 0;
                }
            }

            .item-icon {
                font-size: 35px;
            }

            p {
                margin-top: 15px;
                text-align: center;
                font-size: 20px;
                line-height: 24px;
            }

            &::after {
                content: '';
                display: block;
                width: 145px;
                height: 1px;
                background-color: $orange;

                @include media("<phone") {
                    display: none;
                }
            }
        }
    }
}


/* _______________list-section____________ */

.list-section {

    .container {

        @include media("<phone") {
            padding: 0;
        }
    }

    .list-content {
        padding-bottom: 81px;

        h4 {
            text-align: center;
            margin-bottom: 50px;

            @include media("<phone") {
                margin-bottom: 40px;
            }
        }
    }

    .cards {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 53px;
        margin: 0 auto;

        @include media("<1330px") {
            flex-direction: column;
            justify-content: center;
            align-items: center;

        }
    }

    .card {
        padding: 87px 55px 87px 63px;
        display: flex;
        justify-content: space-between;
        width: 50%;
        max-width: 650px;

        @include media("<1330px") {
            width: 100%;
            max-width: 650px;
        }

        @include media("<tablet") {
            padding: 60px 20px 30px 0;
        }

        @include media("<phone") {
            flex-direction: column;
            align-items: center;
            padding: 23px 0 34px 0;
            min-width: 100%;
        }

        @include media(">phone") {
            &:hover {
                color: $white;

                .btn-orange-bg {
                    background-color: $white;
                    color: $orange;
                }
            }
        }

        .card-icon {
            text-align: center;
            max-width: 170px;

            @include media("<phone") {
                width: 100%;
            }

            .icon-card {
                font-size: 71px;
            }

            h3 {
                font-style: normal;
                font-weight: bold;
                margin: 0 auto;
                margin-top: 45px;

                @include media("<phone") {
                    margin: 15px 0;
                }
            }
        }

        .card-text {
            width: 55%;
            margin-left: 45px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-weight: normal;
            font-size: 14px;
            line-height: 17px;

            @include media("<phone") {
                align-items: center;
                margin: 0;

                p {
                    margin-bottom: 24px;
                }
            }

            @include media("<tablet") {
                margin-left: 0;
            }

            .icon-arrow {
                font-size: 14px;
                text-align: center;
                padding-top: 3px;
            }
        }
    }

    .card-1,
    .card-4 {
        background-color: rgba(248, 249, 250, 1);
    }

    .card-1 {

        @include media("<phone") {
            background: url(../images/list-bg/1.png);
            color: $white;

            .btn-orange-bg {
                background-color: $white;
                color: $orange;
            }
        }

        h3 {
            max-width: 160px;

            @include media("<phone") {
                max-width: 100%;
            }
        }

        @include media(">phone") {

            &:hover {
                background: url(../images/list-bg/1.png);
            }
        }
    }

    .card-2 {

        @include media("<phone") {
            background: url(../images/mob/mob-bg-card.png);
        }

        @include media(">phone") {
            &:hover {
                background: url(../images/list-bg/2.png);
            }
        }
    }

    .card-3 {

        @include media("<phone") {
            background: url(../images/list-bg/1.png);
            color: $white;

            .btn-orange-bg {
                background-color: $white;
                color: $orange;
            }
        }

        h3 {
            max-width: 160px;

            @include media("<phone") {
                max-width: 100%;
            }
        }

        @include media(">phone") {

            &:hover {
                background: url(../images/list-bg/3.png);
            }
        }
    }

    .card-4 {

        @include media("<phone") {
            background: url(../images/mob/mob-bg-card.png);
        }

        @include media(">phone") {
            &:hover {
                background: url(../images/list-bg/4.png);
            }
        }
    }
}

/* _______________problems-section___________________ */

.problems-section {
    margin-bottom: 85px;
    font-size: 20px;

    .problems-content {

        @include media("<phone") {
            display: flex;
            justify-content: space-between;
        }
    }

    .item-wrapper-problem {
        display: flex;
        flex-direction: column;

        @include media("<phone") {
            width: 50%;
            align-items: center;
        }
    }

    .problem {

        @include media("<phone") {
            max-width: 120px;
            margin: 0 0 23px 0;
        }
    }

    .problems-wrapper {
        display: flex;
        justify-content: space-between;
        margin: 40px 0 21px 0;
        text-align: center;

        @include media("<phone") {
            flex-direction: column;
            height: 100%;
            margin: 0;
        }

        div {
            max-width: 30%;

            @include media("<phone") {
                max-width: none;
                min-height: 240px;
            }

            .problems-icon {
                font-size: 35px;
                color: #F95353;

                @include media("<phone") {
                    display: none;
                }
            }

            p {
                margin: 15px 0 0 0;

                @include media("<phone") {
                    margin: 0 0 15px 0;
                }
            }
        }
    }

    .line-wrapper {

        .line {
            display: block;
            background-color: black;
            width: 100%;
            height: 1px;

            @include media("<phone") {
                width: 1px;
                height: 100%;
                margin: 0 15px;
            }
        }
    }

    .item-wrapper-solution {
        display: flex;
        flex-direction: column;

        @include media("<phone") {
            width: 50%;
        }
    }

    .solution {
        margin: 40px 0 40px 0;
        align-self: flex-end;

        @include media("<phone") {
            align-self: center;
            margin: 0 0 23px 0;
            max-width: 120px;
        }
    }

    .solutions-wrapper {
        display: flex;
        justify-content: space-between;
        text-align: center;

        @include media("<phone") {
            flex-direction: column;
            height: 100%;
            margin: 0;
        }

        div {
            max-width: 30%;

            @include media("<phone") {
                max-width: none;
                min-height: 240px;
            }

            .solutions-icon {
                font-size: 35px;
                color: #00BE4C;

                @include media("<phone") {
                    display: none;
                }
            }

            p {
                margin: 15px 0 0 0;

                @include media("<phone") {
                    margin: 0 0 15px 0;
                }
            }
        }
    }

    .btn-orange-bg {
        display: block;
        margin: 0 auto;
        margin-top: 40px;
        width: 250px;

        &:hover {
            background-color: #352F2D;
            color: $white;
            border-color: #352F2D;
        }

        @include media("<phone") {
            display: none;
        }
    }
}

/* ___________our-projects-section______________ */

.our-projects-section {
    margin-bottom: 70px;

    h4 {
        text-align: center;
    }

    .mob-swiper {
        display: flex;

        .slide1 {
            background: linear-gradient(0deg, rgba(235, 91, 49, 0.8) 41.12%, rgba(235, 91, 49, 0) 100%), url(../images/Our-projects/1.png) 50%/cover no-repeat;
        }

        .slide2 {
            background: linear-gradient(0deg, rgba(235, 91, 49, 0.8) 41.12%, rgba(235, 91, 49, 0) 100%), url(../images/Our-projects/2.png) 50%/cover no-repeat;
        }

        .slide3 {
            background: linear-gradient(0deg, rgba(235, 91, 49, 0.8) 41.12%, rgba(235, 91, 49, 0) 100%), url(../images/Our-projects/3.png) 50%/cover no-repeat;
        }

        .slide4 {
            background: linear-gradient(0deg, rgba(235, 91, 49, 0.8) 41.12%, rgba(235, 91, 49, 0) 100%), url(../images/Our-projects/4.png) 50%/cover no-repeat;
        }

        .slide5 {
            background: linear-gradient(0deg, rgba(235, 91, 49, 0.8) 41.12%, rgba(235, 91, 49, 0) 100%), url(../images/Our-projects/5.png) 50%/cover no-repeat;
        }

        .text-slide {
            color: $white;
            margin: 0 auto;
            width: 60%;
            padding: 380px 24px 46px 24px;

            @include media("<tablet") {
                width: 85%;
            }

            p {
                text-align: justify;
            }

            .btn-projects {
                display: block;
                margin: 0 auto;
                max-width: 272px;
                margin-top: 39px;
                background-color: $white;
                color: $orange;
                text-align: center;
                font-size: 16px;
                padding: 13px 0;
                border-radius: 3px 3px 0px 0px;
            }
        }

        .swiper-pagination {

            .swiper-pagination-bullet {
                background-color: $white;
                opacity: 1;
                width: 10px;
                height: 10px;
            }

            .swiper-pagination-bullet-active {
                border: 2px solid #FFFFFF;
                background-color: hsla(210, 17%, 98%, 0);
            }
        }

        .swiper-button-prev,
        .swiper-button-next {
            color: $white;

            @include media("<tablet") {
                display: none;
            }
        }
    }

    .btn-orange-sm {
        display: block;
        margin: 0 auto;
        max-width: 149px;
        margin-top: 40px;
    }
}


/* _______________________awards-section_____________________ */

.awards-section {

    .container {

        @include media("<phone") {
            padding: 0;
        }
    }

    h4 {
        text-align: center;
    }

    .awards-wrap {
        display: flex;
        justify-content: space-between;
        text-align: center;
        margin-bottom: 79px;

        @include media("<1330px") {
            flex-wrap: wrap;
            justify-content: center;
        }

        @include media("<phone") {
            justify-content: center;
            width: 303px;
            margin: 0 auto;
            margin-bottom: 72px;
        }
    }

    .a-card {
        padding: 50px 60px 57px 60px;
        background-color: #F15A24;
        color: #fff;
        font-size: 22px;
        min-width: 310px;
        border-radius: 6px;
        margin-right: 20px;

        @include media("<1330px") {
            margin: 20px 10px 0;
        }

        @include media("<phone") {
            background-color: hsla(16, 88%, 54%, 0);
            min-width: 0;
            min-height: 0;
            padding: 0 41px 19px 10px;
            margin: 0;
            border-color: $orange;
            border-style: solid;
            border-radius: 0;
            border-width: 0 1px 1px 0;

            &:nth-child(even) {
                padding: 0 10px 0 41px;
                border-width: 0 0 1px 0;
            }

            &:nth-child(3n),
            &:nth-child(4n) {
                padding-top: 19px;
                border-width: 0 1px 0 0;
            }
        }

        &:last-child {
            margin-right: 0;

            @include media("<1330px") {
                margin-right: 10px;
            }

            @include media("<phone") {
                margin: 0;
                border-width: 0 0 0 0;
            }

        }

        img {
            max-width: 190px;

            @include media("<phone") {
                max-width: 90px;
            }
        }

        p {
            margin-top: 66px;

            @include media("<phone") {
                display: none;
            }
        }
    }

    .a-card2 {
        padding: 69px 45px 72px 45px;

        @include media("<phone") {
            padding: 0;
        }
    }
}

/* __________________todiscuss-section_____________ */

.todiscuss-section {
    padding-bottom: 130px;

    @include media("<tablet") {
        padding-bottom: 75px;
    }

    h3 {
        margin-bottom: 45px;
        text-align: center;
    }

    .content-wrap {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        @include media("<desktop") {
            flex-direction: column;
            align-items: center;
            text-align: center;
        }
    }

    .todiscuss-text-wrap {
        max-width: 50%;
        font-size: 16px;
        line-height: 21px;

        @include media("<tablet") {
            max-width: 100%
        }

        p {
            margin: 0;
            padding-right: 10px;
        }
    }

    form {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        margin-left: 144px;

        @include media("<desktop") {
            margin: 40px 0 0 0;
        }

        input {
            padding: 11px;
            height: 44px;
            border: 1px solid #EB5B31;
            box-sizing: border-box;
            border-radius: 3px;
            background: $white;
            text-align: center;
            margin-bottom: 16px;
            color: $orange;

            &:focus {
                border: 1px solid #EB5B31;
            }
        }

        .btn-form {
            max-height: 50px;
            padding: 15px 59px;
            background: #EB5B31;
            border-radius: 3px;
            border: none;
            outline: none;
            color: #fff;
            font-size: 16px;
            line-height: 20px;
            font-weight: 600;

            &:hover {
                background-color: #352F2D;
                color: $white;
                border-color: #352F2D;
            }
        }
    }
}

/* ________________thebook-section_______________________ */

.thebook-section {
    position: relative;
    background: url(../images/orenge-bg.png) bottom left/ 35% 85% no-repeat;
    padding-bottom: 50px;
    margin-bottom: -5px;

    @include media("<tablet") {
        background: url(../images/orenge-bg.png) bottom/ 100% 25% no-repeat;
        padding-bottom: 152px;
    }

    .bg-book {
        position: absolute;
        right: -195px;
        top: 280px;
        color: $orange;
        font-weight: bold;
        font-size: 90px;
        line-height: 110px;
        transform: rotate(-90deg);
        margin: 0;
        z-index: -1;

        @include media("<desktop") {
            display: none;
        }
    }

    .title {
        text-align: center;
        margin: 0 auto;

        h4 {
            margin: 0 0 6px 0;
        }

        p {
            font-size: 20px;
            margin-bottom: 50px;

            @include media("<tablet") {
                margin-bottom: 47px;
            }
        }
    }

    .item-wrap {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 50px 0 0 0;

        @include media("<desktop") {
            justify-content: space-between;
        }

        @include media("<tablet") {
            flex-direction: column;
            padding: 0;
        }
    }

    .img-bg {

        @include media("<tablet") {
            order: 2;
        }
    }

    .text-wrap {
        margin-left: 133px;
        min-width: 360px;

        @include media("<desktop") {
            margin-left: 25px;
        }

        @include media("<tablet") {
            order: 1;
            margin: 0 0 63px 0;
            max-width: 360px;
            min-width: 0;
        }

        div {
            display: flex;
            margin-bottom: 30px;

            @include media("<tablet") {
                flex-direction: column;
                align-items: center;
                text-align: center;
            }
        }

        i {
            color: $orange;
            font-size: 40px;
        }

        p {
            margin: 0 0 0 51px;
            font-size: 20px;
            line-height: 24px;
            max-width: 360px;

            @include media("<tablet") {
                margin: 12px 0 0 0;

            }
        }
    }

    .btn-orange-bg {
        width: 300px;
        margin: 40px 0 0 0;

        &:hover {
            background-color: #352F2D;
            color: $white;
            border-color: #352F2D;
        }

        @include media("<tablet") {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 40px;
            background: $white;
            color: $orange;
            justify-content: space-evenly;
        }

        @include media("<phone") {
            width: 80%;
        }

        .icon-arrow {
            color: $white;
            font-size: 14px;
            padding-top: 3px;

            @include media("<tablet") {
                color: $orange;
            }
        }
    }
}

/* _________________about-us-section______________ */

.about-us-section {
    padding: 169px 0 130px 0;

    @include media("<1110px") {
        padding: 100px 0 80px 0;
    }

    .about-us-content {
        text-align: center;

        h1 {
            margin: 106px 0 43px 0;
            color: $orange;

            @include media("<tablet") {
                font-size: 24px;
                margin: 0 0 24px 0;
            }
        }
    }

    .main-img-text {
        width: 100%;
        background-color: $orange;
        padding: 16px 0 19px 0;
        color: #fff;
        margin-bottom: 56px;

        h3 {
            margin: 0;
            padding: 0 10px;

            @include media("<phone") {
                font-size: 12px;
            }
        }
    }

    .about-us-text,
    .our-wrap {
        display: flex;
        justify-content: space-around;
        font-size: 24px;
        line-height: 150%;
        text-align: justify;
        flex-wrap: wrap;

        @include media("<tablet") {
            font-size: 20px;
        }

        @include media("<phone") {
            font-size: 18px;
        }

        p {
            max-width: 500px;
            margin: 0 15px 40px 15px;

            @include media("<tablet") {
                margin: 0 15px 60px 15px;
            }

            @include media("<phone") {
                margin: 0;
            }
        }

        h3 {
            color: $orange;
            margin-left: 15px;

            @include media("<1110px") {
                text-align: center;
                margin: 50px 0 20px 0;
            }
        }
    }

    .btn-orange-sm {

        @include media("<phone") {
            margin-top: 35px;
            width: 80%;
        }
    }

    .our-wrap {
        position: relative;
        margin-top: 90px;

        @include media("<1110px") {
            margin: 0;
        }

        p {
            margin: 0 15px;

            @include media("<phone") {
                margin: 0;
            }
        }

        .vertical-line {
            background: #C4C4C4;
            width: 1px;

            @include media("<1110px") {
                display: none;
            }
        }
    }
}

/* __________________________top5-section_________________________ */

.top5-section {
    margin-bottom: 11px;
    text-align: center;

    h2 {
