            align-items: center;
        }
    }

    .card {
        display: flex;
        flex-direction: column;
        margin-bottom: 40px;
        height: 100vh;
        max-height: 515px;

        @include media("<tablet") {
            position: relative;
            height: auto;
            max-height: none;

            &::after {
                content: '';
                position: absolute;
                height: 1px;
                width: 30%;
                background-color: #C4C4C4;
                bottom: -17px;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }

    .card-content {
        position: relative;
        display: flex;
        flex-direction: column;

        @include media("<tablet") {
            order: 1;
            height: auto;
            max-height: none;
        }

        @include media(">tablet") {

            &:hover {

                .hover-wrap {
                    opacity: 1;
                }
            }
        }

        .card-img-wrap {
            position: absolute;
            z-index: -1;
            overflow: hidden;
            width: 100vh;
            max-width: 310px;
            height: 100vh;
            max-height: 450px;

            @include media("<tablet") {
                position: relative;
                order: 1;
            }

            img {
                object-fit: cover;
                object-position: center;
                height: 100%;
            }
        }

        .hover-wrap {
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            height: 100%;
            padding: 25px;
            color: $white;
            background: $orange;
            opacity: 0;
            transition: all 0.3s ease-in-out;
            width: 100vh;
            max-width: 310px;
            height: 100vh;
            max-height: 450px;

            @include media("<tablet") {
                opacity: 1;
                background: none;
                padding: 0;
                order: 3;
                height: auto;
            }

            h3 {

                @include media("<tablet") {
                    display: none;
                }

            }

