                font-size: 18px;
                color: $white;

                @include media("<tablet") {
                    margin: 20px 0;
                    color: black;
                }
            }

            .btn-orange-sm {
                border-color: $white;
                align-self: center;

                @include media("<tablet") {
                    order: 4;
                    border: none;
                }
            }
        }

        .card-text-wrap {

            @include media("<tablet") {
                order: 2;
                display: flex;
                flex-direction: column;
                text-align: center;
            }

            p {
                margin: 8px 0 4px 0;
                color: #8C8C8C;

                @include media("<tablet") {
                    order: 2;
                }
            }

            h3 {

                @include media("<tablet") {
                    order: 1;
                    margin: 30px 0 15px 0;
                }
            }
        }
    }

    .btn-orange-sm {
        align-self: center;
    }

