        background: transparent;
        border: 1px solid $orange;
        position: relative;
        transition: all 0.6s ease;

        &::after {
            content: '';
            position: absolute;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background: $orange;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

