        position: relative;
        width: 49.5%;
        height: 550px;
        border-radius: 3px;
        overflow: hidden;

        @include media ("<minscreen") {
            height: 450px;
            width: 49.3%;
        }

        @include media ("<tablet") {
            width: 100%;
        }

        @include media ("<phone") {
            height: 280px;
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .web {
        display: flex;
        padding: 35px 0;

        @include media ("<desktop") {
            flex-direction: column;
            align-items: center;
        }

        @include media ("<tablet") {
            padding: 20px 0;
        }

