        width: 100%;
        max-width: 50%;
        border-radius: 3px;
        overflow: hidden;

        @include media ("<desktop") {
            max-width: 100%;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .arrow {
        display: flex;
        justify-content: space-between;

        @include media ("<tablet") {
            padding: 0 15px;
        }

        a {
            font-weight: 700;
            font-size: 24px;
            line-height: 29 / 24 * 100%;
            text-decoration-line: underline;
            color: #1B1B1B;
            transition: all 0.4s ease;

            &:hover {
                color: $orange;

                svg {
                    fill: $orange;
                }
            }

            @include media ("<tablet") {
                font-size: 0;
            }

            svg {
                fill: #1B1B1B;
                transition: all 0.4s ease;
            }
        }

        img {
            vertical-align: middle;

            @include media ("<tablet") {
                width: 25px;
                height: 35px;
            }

            &:first-child {
                margin-right: 15px;
            }

            &:last-child {
                margin-left: 15px;
            }
        }
    }

    .video-block {
        display: none;
        position: absolute;
        left: 50%;
        width: 100%;
        transform: translate(-50%);
        height: max-content;
        text-align: center;
        z-index: 3;

        img {
            width: 90%;
            height: auto;

            @include media ("<desktop") {
                width: 100%;
            }
        }
    }

    .video-block_active {
        display: block;
    }

    .close-video {
        position: absolute;
        top: 10px;
        right: 1%;
        width: 30px;
        height: 30px;
        background: white;
        border-radius: 50%;
        opacity: .6;
        transition: all .3s ease;

        &:hover {
            opacity: 1;
        }

        &::before,
        &::after {
            content: '';
            position: absolute;
            top: 14px;
            left: 8px;
            width: 15px;
            height: 2px;
            background: $black;
        }

        &::before {
            transform: rotate(45deg);
        }

        &::after {
            transform: rotate(-45deg);
        }
    }
}

#wrapper {
    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        z-index: 2;

