/* ________________________contact-section____________________ */

.contact-section {
    padding: 275px 0 31px 0;
    text-align: center;

    h1 {
        color: $orange;
        margin-bottom: 135px;

        @include media("<phone") {
            margin-bottom: 37px;
        }
    }

    h3 {
        margin-bottom: 105px;

        @include media("<phone") {
            margin-bottom: 37px;
        }
    }

    .contact-wrap {
        display: flex;
        justify-content: space-between;

        @include media("<1100px") {
            flex-direction: column;
            align-items: center;
        }
    }

    form {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        max-width: 405px;

        input {
            border: 1px solid $orange;
            margin-bottom: 30px;
            padding: 14px 15px;
            text-align: center;
            color: $orange;

            &::placeholder {
                color: #DBDBDB;
                font-size: 20px;
            }

            &:focus {
                border: 1px solid $orange;
            }
        }

        textarea {
            min-height: 175px;
            border: 1px solid $orange;
            margin-bottom: 40px;
            padding: 10px 20px;
            color: $orange;
        }

        .btn-form-wrap {
            display: flex;

            .btn-form {
                max-width: 160px;
                max-height: 50px;
                padding: 15px 59px;
                background: $orange;
                border-radius: 3px;
                border: none;
                outline: none;
                color: $white;
                font-size: 16px;
                line-height: 20px;
                font-weight: 600;
                display: block;

                &:hover {
                    background-color: #352F2D;
                    color: $white;
                    border-color: #352F2D;
                }
            }

            p {
                align-self: center;
                margin: 0 0 0 30px;
                text-align: justify;
                font-size: 10px;
                line-height: 12px;
            }
        }
    }

    .map-wrap {
        display: flex;
        flex-direction: column;
        margin: 0 0 0 70px;
        flex-grow: 1;
        position: relative;

        @include media("<1100px") {
            margin: 50px 0 0 0;
            align-items: center;
        }
    }

    .adres-wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 18px;
        max-width: 100%;

        @include media("<1100px") {
            justify-content: center;
        }

        @include media("<tablet") {
            flex-direction: column;
            align-items: center;
        }
    }

    .adres-item {
        font-size: 18px;
        font-weight: 600;
        text-align: start;

        @include media("<tablet") {
            text-align: center;
            margin: 24px 0 0 0;
        }

        a {
            color: black;
        }

        i {
            margin: 0 7px 0 0;
            font-size: 15px;
        }
    }

    .tel-wrap {
        max-width: 50%;

        @include media("<tablet") {
            max-width: 100%;

        }
    }

    .geo-wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        max-width: 50%;

        @include media("<tablet") {
            max-width: 100%;
        }

        .first-adres,
        .second-adres {
            margin: 0 0 24px 0;
        }

        i {
            margin-top: 5px;
        }
    }

    .emeil1-wrap,
    .emeil2-wrap {
        margin: 24px 20px 0 0;
    }

    #map {
        max-height: 350px;
        max-width: 100%;
    }
}

/* ________________________work-section__________________________ */

.work-section {
    text-align: center;
    background: url(../images/contact/work-bg.jpg) 50%/cover no-repeat;
    padding: 47px 0 42px 0;

    .work-content {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    p {
        max-width: 446px;
        margin: 16px 0 38px 0;
    }

    form {
        display: flex;
        flex-wrap: wrap;
        max-width: 646px;

        @include media("<tablet") {
            flex-direction: column;
            align-items: center;
        }

        input {
            width: 284px;
            height: 44px;
            padding: 11px 10px;
            background: #EB5B31;
            box-shadow: inset 0px 2px 7px rgba(0, 0, 0, 0.25);
            border-radius: 3px;
            outline: none;
            font-size: 14px;
            line-height: 17px;
            border: none;
            color: $white;
            margin: 0 13px 20px 13px;

            &::placeholder {
                color: $white;
            }
        }

        .btn-form {
            margin: 0 13px;
            width: 92%;
            height: 35px;
            font-weight: 600;
            font-size: 12px;
            line-height: 15px;
            color: $orange;
            background: #FFFFFF;
            border: 1px solid #EB5B31;
            box-sizing: border-box;
            border-radius: 3px;
            outline: none;

            &:hover {
                background-color: #352F2D;
                color: $white;
                border-color: #352F2D;
            }
        }
    }
}

